@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
	font-family: 'AC7-Regular';
	src: local('AC7-Regular'), url(./fonts/ACES07_Regular.otf) format('opentype');
}

@font-face {
	font-family: 'ACES04';
	src: local('ACES04'), url(./fonts/ACES04.ttf) format('truetype');
}

@font-face {
	font-family: 'OCR-A';
	src: local('OCR-A'), url(./fonts/ocr-aregular.ttf) format('truetype');
}


body {
	margin: 0;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-family: 'AC7-Regular', sans-serif;

	color: rgb(168, 255, 255);
	text-shadow: 0 0 5px rgb(168, 255, 255), 0 0 15px #228dff, 0 0 35px #228dff;
	background: url('./images/ac7-bg-img.jpg') no-repeat center center fixed;
	background-color: rgb(12,35,87);
	
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;

	-webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

body.ac7-theme {
	color: rgb(168, 255, 255);
	text-shadow: 0 0 5px rgb(168, 255, 255), 0 0 15px #228dff, 0 0 35px #228dff;
	background: url('./images/ac7-bg-img.jpg') no-repeat center center fixed;
	background-color: rgb(12,35,87);

	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
}

body.acz-theme {
	color: rgb(235, 188, 31);
	text-shadow: 0 0 5px rgb(255, 219, 151), 0 0 15px #b6730f, 0 0 35px #923600;
	background: url('./images/acz-bg-img.jpg') no-repeat center center fixed;
	background-color: rgb(87, 12, 12);

	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
}

body.ac5-theme {
	color: rgb(24, 207, 131);
	text-shadow: 0 0 5px rgb(24, 207, 131), 0 0 15px #0fb668, 0 0 35px #009238;
	background: url('./images/ac5-alike-bg-img.jpg') no-repeat center center fixed;
	background-color: rgb(11, 97, 71);

	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
}

#modal-begin {
	background-color: rgb(35, 35, 41);
	z-index: 10;
}

.begin-button {
	text-align: center;
	-webkit-align-self:center;-moz-align-self:center;align-self:center;
}

.no-text-glow {
	text-shadow: none;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}


.ac7-theme .breadcumb-logo {
	width: 60px;
	height: 60px;
	border: solid #fff 1px;
	box-shadow: 0 0 5px rgb(168, 255, 255), 0 0 15px #228dff, 0 0 35px #228dff;
}

.acz-theme .breadcumb-logo {
	width: 60px;
	height: 60px;
	color: rgb(235, 188, 31);
	text-shadow: 0 0 5px rgb(255, 219, 151), 0 0 15px #b6730f, 0 0 35px #923600;
}

.ac5-theme .breadcumb-logo {
	width: 60px;
	height: 60px;
	color: rgb(24, 207, 131);
	text-shadow: 0 0 5px rgb(24, 207, 131), 0 0 15px #0fb668, 0 0 35px #009238;
}