
.main-home-page-container {
    border-left: 1px solid rgb(168, 255, 255);;
}

.ac7-theme .inactive-list-item {
	color: rgb(90, 90, 90);
	text-shadow: none;
}

.acz-theme .inactive-list-item {
	color: rgb(135, 35, 1);
	text-shadow: none;
}

.ac5-theme .inactive-list-item {
	color: rgb(7, 112, 72);
	text-shadow: none;
}

.ac7-theme .inactive-list-item:hover {
	color: rgb(168, 255, 255);
	text-shadow: 0 0 5px rgb(168, 255, 255), 0 0 15px #228dff, 0 0 35px #228dff;
}

.acz-theme .inactive-list-item:hover {
	color: rgb(235, 188, 31);
	text-shadow: 0 0 5px rgb(255, 219, 151), 0 0 15px #b6730f, 0 0 35px #923600;
}

.ac5-theme .inactive-list-item:hover {
	color: rgb(24, 207, 131);
	text-shadow: 0 0 5px rgb(24, 207, 131), 0 0 15px #0fb668, 0 0 35px #009238;
}

.keycap-box {
	min-width: 42px;
	min-height: 42px;
	border: 1px solid white;
	border-radius: 5px;
}